      <img v-else class="icon" id="drink" src="../assets/drink@2x.png">
      <div class="label">Drink!</div>
    </router-link>
    <router-link to="/progress">
      <img v-if="currentRouteName === 'Progress'" class="icon" id="calendar" src="../assets/calendar_active@2x.png">
      <img v-else class="icon" id="calendar" src="../assets/calendar@2x.png">
      <div class="label">My Progress</div>
    </router-link>
    <router-link to="/settings">
      <img v-if="currentRouteName === 'Settings'" class="icon" id="settings" src="../assets/settings_active@2x.png">
      <img v-else class="icon" id="settings" src="../assets/settings@2x.png">
      <div class="label">Preferences</div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'Tabbar',
  computed: {
    currentRouteName () {
      return this.$route.name
    }
  }
}
</script>

<style>
#tabbar {
