  height: 60px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0.2em 0 1.1em;
  border-top: solid #b2b2b2 0.001em;
  z-index: 1000;
  background: #fff;
  -webkit-overflow-scrolling:touch;
}
#tabbar a {
