// TrackingService for Working with TrackItems and Events
import { LocalStorageService } from './LocalStorageService'

export const STORAGE_KEY = "settingsItems"

export const SettingsService = {
  //Store a new Item inside the Local Storage
  storeAll (items) {
    LocalStorageService.setItem(STORAGE_KEY, items)
  },
  fetchAllWithDeleted () {
    return LocalStorageService.getItem(STORAGE_KEY, [])
  },
  fetchAll () {
    return SettingsService.fetchAllWithDeleted().filter(item => !item.deletedAt)
  },
  updateItem (itemId, payload) {
    const items = SettingsService.fetchAllWithDeleted()
    const index = items.findIndex(i => i.id === itemId)
    if (index === -1) return
    const item = {"id": itemId, "value": payload};
    items.splice(index, 1, item)
    SettingsService.storeAll(items)
  },
}