<template>
  <div id="progressView">
    <div id="progressHeader">My Progress</div>
    <div id="progressCalender">
      <ul id="label">
        <li>Mon</li>
        <li>Tue</li>
        <li>Wed</li>
        <li>Thu</li>
        <li>Fri</li>
        <li>Sat</li>
        <li>Sun</li>
      </ul>
      <div class="day" v-for="day in calendarItems" :key="day.id" @click="selectDay(day)" :class="{highlight:day.id == selected}"><div :style="{ 'opacity': day.track / 100}" class="track"></div></div>
    </div>
    <div id="progressDetail">
      <p>{{ selectedDay }}</p>
      <p id="progress" v-if="selectedProgress == '100'">Goal Met!</p>
      <p id="progress" v-else-if="selectedProgress == '0' || selectedProgress != ''">{{ selectedProgress }} %</p>
      <p id="progress" v-else></p>
    </div>
  </div>
</template>

<script>
 import moment from 'moment'
 import { TrackingService } from "@/services/TrackingService"

export default {
  name: "Progress",
  data () {
    return {
      trackingItems: [],
      calendarItems: [],
      selectedDay: "",
      selectedProgress: "",
      selected: ""
    }
  },
  mounted () {
    this.fetchTrackingData()
    this.calendarItems = this.generateCalendarData()
  },
  watch: {
    'calendarItems': 'scrollToLast'
  },
  methods: {
    getDates(startPar, stopPar) {
        moment.locale('de');
        var dateArray = [];
        var currentDate = moment(startPar);
        var stopDate = moment(stopPar);
        while (currentDate <= stopDate) {
            dateArray.push({ id: this.generateRandom(), date: moment(currentDate).format('YYYY-MM-DD'), track: this.calculateProgressOfDay(moment(currentDate).format('YYYY-MM-DD'))})
            currentDate = moment(currentDate).add(1, 'days');
        }
        return dateArray;
    },
    generateCalendarData() {
      var daylist = this.getDates(new Date("2020-01-01"), new Date());

      // Hinzufügen von zwei weiteren Tagen um an einem Montag zu starten
      daylist.unshift({ id: this.generateRandom(), date: moment(new Date("2019-12-31")).format('YYYY-MM-DD'), track: this.calculateProgressOfDay("2019-12-31") });
      daylist.unshift({ id: this.generateRandom(), date: moment(new Date("2019-12-30")).format('YYYY-MM-DD'), track: this.calculateProgressOfDay("2019-12-30") });

      return daylist
    },
    // Methode zum Hervorheben eines Tages in der Übersicht 
    selectDay(day) {
      this.selected = day.id
      this.selectedDay = this.format_date(day.date)
      this.selectedProgress = day.track
    },
    format_date(value){
      moment.locale('de');    
      if (value) {
        return moment(String(value)).format('LL')
      }
    },
    generateRandom() {
      return Math.random().toString(36).substr(2, 7);
    },
    calculateProgressOfDay (date) {
      var dailyItems = this.trackingItems.filter(item => new Date(item.createdAt).getDate() === new Date(date).getDate() && new Date(item.createdAt).getMonth() === new Date(date).getMonth() && new Date(item.createdAt).getFullYear() === new Date(date).getFullYear())
      //console.log(dailyItems.reduce((acc, item) => acc + item.value, 0));
      return dailyItems.reduce((acc, item) => acc + item.value, 0);
    },
    fetchTrackingData() {
      this.trackingItems = TrackingService.fetchAll()
    },
    scrollToLast() {      
      this.$nextTick(function () {
        let chat = document.getElementById('progressCalender');
        //let shouldScroll = chat.scrollWidth - chat.clientWidth <= chat.scrollLeft + 1;
        
        console.log(
          window.getComputedStyle(chat).getPropertyValue("width")
        )
        
        chat.scrollLeft = chat.scrollWidth;
      });
    }
  }
};
</script>

<style>
#progressView {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: -webkit-fill-available;
  overflow: hidden !important;
  margin: 100px 0 0;
  white-space: nowrap;
}
#progressHeader {
  font-family: "Kaushan Script", cursive;
  color: #327bd0;
  font-size: 24px;
  font-weight: 700;
  width: 100%;
  position: fixed;
  top: 10%;
}
#progressCalender {
  display: flex;
  flex-direction: column;
  height: 197px;
  flex-wrap: wrap;
  width: -webkit-fill-available;
  overflow-y: auto;
  position: fixed;
  top: 21%;
  -webkit-overflow-scrolling: touch;
  padding: 0 10px 0 30px;
  margin: 0 10px 0 0;
}
#progressCalender::-webkit-scrollbar {
  display: none;
  height: 0;
  width: 0;
}
.day {
  width: 20px;
  height: 20px;
  background: #ebf0fa;
  margin: 3px;
  border-radius: 4px;
  border: 1px solid #fff;
  overflow: hidden;
}
.day.highlight {
  border: 1px solid #b2b2b2;
}
.day .track {
  width: 20px;
  height: 20px;
  display: block;
  background:#387dcf;
}
#progressDetail {
  position: fixed;
  width: 100%;
  top: 52%;
  color: #327bd0;
  font-size: 18px;
}
#progress {
  font-family: "Kaushan Script", cursive;
  font-size: 26px;
  font-weight: 700;
}
ul#label {
  list-style: none;
  margin: 0;
  padding: 0;
  background: #fff;
  position: fixed;
  display: flex;
  flex-direction: column;
  left: 0px;
  z-index: 1000;
}
ul#label li {
  height: 20px;
  padding: 0;
  font-size: 12px;
  color: #b2b2b2;
  border: none;
  margin: 3px;
  border: 1px solid #fff;
}
ul#label li:first-child {
  background: #fff;
  text-transform: none;
}
ul#label li .track {
  background: red;
}
@media (prefers-color-scheme: dark) {
  ul#label {
    background: #191926;
  }
  ul#label li {
    background: #191926;
    border: 1px solid #191926;
  }
  ul#label li:first-child {
    background: #191926;
  }
  .day.highlight {
    border: 1px solid #627496;
  }
  .day .track {
    background:#255ab1;
  }
  .day {
    background: #1b2033;
    border: 1px solid #191926;
  }
  #progressDetail {
    color: #235ab1;
  }
}
</style>
