// TrackingService for Working with TrackItems and Events
import { LocalStorageService } from './LocalStorageService'

export const STORAGE_KEY = "trackingItems"

export const TrackingService = {
  //Store a new Item inside the Local Storage
  storeAll (items) {
    LocalStorageService.setItem(STORAGE_KEY, items)
  },
  fetchAllWithDeleted () {
    return LocalStorageService.getItem(STORAGE_KEY, [])
  },
  fetchAll () {
    return TrackingService.fetchAllWithDeleted().filter(item => !item.deletedAt)
  }
}