<template>
  <div id="settingsView">
    <div id="header">Preferences</div>
    <ul>
      <li>Support Gulps</li>
      <li class="link" @click="openExternalLink('https://www.google.de/')">
        <label>Support Development</label>
        <img src="@/assets/rightArrowIcon.png" />
      </li>
      <li class="link" @click="openExternalLink('https://apps.apple.com/us/app/gulps/id979057304')">
        <label>Rate Gulps</label>
        <img src="@/assets/rightArrowIcon.png" />
      </li>
    </ul>
    <ul>
      <li>Settings</li>
      <li class="picker">
        <label>Unit of mesure</label>
        <span>Liters</span>
      </li>
      <li class="picker">
        <label>First day of the week</label>
        <span>Monday</span>
      </li>
    </ul>
    <ul>
      <li>Portions</li>
      <li class="setting">
        <label>Small gulp</label>
        <input inputmode="decimal" @blur="updateSettings('smallGulp', $event)" v-model="smallGulp">
        <span>L</span>
      </li>
      <li class="setting">
        <label>Big gulp</label>
        <input inputmode="decimal" @blur="updateSettings('bigGulp', $event)"  v-model="bigGulp">
        <span>L</span>
      </li>
      <li class="setting">
        <label>Daily goal</label>
        <input inputmode="decimal" @blur="updateSettings('dailyGoal', $event)"  v-model="dailyGoal">
        <span>L</span>
      </li>
    </ul>
    <ul>
      <li>Notifications</li>
      <li class="toggle">
        <span>Remind me to drink</span>
        <toggle-button :value="pushToggle" :sync="true" color="#235ab1" @change="onChangeToggle('push')"/>
      </li>
      <li class="picker">
        <label>From</label>
        <span>10:00</span>
      </li>
      <li class="picker">
        <label>To</label>
        <span>22:00</span>
      </li>
      <li class="picker">
        <label>Every</label>
        <span>2 hours</span>
      </li>
    </ul>
    <ul v-if=isiOS()>
      <li>Apple Health</li>
      <li class="link" @click="exportHealthData">
        <label>Export data to Health</label>
        <img src="@/assets/exportDataIcon.png" />
      </li>
    </ul>
    <ul v-else>
      <li>Health Data</li>
      <li class="link" @click="exportHealthData">
        <label>Export all Health Data</label>
        <img src="@/assets/exportDataIcon.png" />
      </li>
    </ul>
  </div>
</template>

<script>
import { SettingsService } from "@/services/SettingsService"
import { ToggleButton } from 'vue-js-toggle-button'

export default {
  name: "Settings",
  components: {
    ToggleButton
  },
  data () {
    return {
      settingsItems: [],
      smallGulp: "0,2",
      bigGulp: "0,5",
      dailyGoal: "2",
      initialSettingsItems: [{"id": "unitOfMesure", "value": "Liters"},{"id": "firstDayOfWeek", "value": "Liters"},{"id": "smallGulp", "value": "0.2"},{"id": "bigGulp", "value": "0.5"},{"id": "dailyGoal", "value": "2"}],
      pushToggle: false
    }
  },
  mounted () {
    this.fetchSettingsData()
    this.smallGulp = this.settingsItems.find(item => item.id === "smallGulp").value
    this.bigGulp = this.settingsItems.find(item => item.id === "bigGulp").value
    this.dailyGoal = this.settingsItems.find(item => item.id === "dailyGoal").value
  },
  methods: {
    isiOS() {
      const userAgent = window.navigator.userAgent.toLowerCase();
      return /iphone|ipad|ipod/.test( userAgent );
    },
    openExternalLink(linkURL) {
      window.open(linkURL, "_blank");
    },
    updateSettings(itemId, event) {
      const value = event.target.value
      SettingsService.updateItem(itemId, value)
      this.fetchSettingsData()
    },
    fetchSettingsData () {
      this.settingsItems = SettingsService.fetchAll()
      if (this.settingsItems.length < 1) {
        this.settingsItems = this.initialSettingsItems
      }
      // store the items
      this.storeItems()
    },
    storeItems() {
      SettingsService.storeAll(this.settingsItems)
    },
    onChangeToggle(source) {
      if (source == 'push') {
        if (this.isiOS()) {
          alert("Push-Benachrichtigungen sind unter iOS nicht verfügbar.")
        } else {
          this.pushToggle = !this.pushToggle
        }
      }
    },
    exportHealthData() {
      const rows = [["name1", "city1", "some other info"],["name2", "city2", "more info"]];

      let csvContent = "data:text/csv;charset=utf-8," + rows.map(e => e.join(",")).join("\n");

      var encodedUri = encodeURI(csvContent);
      var link = document.createElement("a");
      link.setAttribute("href", encodedUri);
      link.setAttribute("download", "Gulps_AppleHealthData.csv");
      document.body.appendChild(link); // Required for FF

      link.click(); // This will download the data file named "my_data.csv".
    },
  }
};
</script>

<style>
#settingsView {
  position: relative;
  width: 100%;
  background: #f2f2f2;
  overflow: hidden !important;
}
#header {
  font-family: "Kaushan Script", cursive;
  color: #327bd0;
  font-size: 24px;
  font-weight: 700;
  position: fixed;
  height: 80px;
  width: 100%;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border-bottom: solid #b2b2b2 0.001em;
  padding: 0 0 10px;
  z-index: 1000;
}
ul {
  list-style: none;
  text-align: left;
  margin: 30px 0 10px;
  padding: 0;
  background: #f2f2f2;
}
ul:first-of-type {
  padding: 80px 0 10px;
}
ul:last-of-type {
  padding: 0 0 120px;
}
ul li:first-child {
  text-transform: uppercase;
  color: #6d6c71;
  font-size: 12px;
  padding: 0 14px 6px;
  background: #f2f2f2;
  border-bottom: 0;
}
ul li {
  background: #fff;
  color: #1a1920;
  padding: 10px 14px;
  font-size: 16px;
  border-bottom: solid #b2b2b2 0.001em;
  display: flex;
  flex-direction: row;
  align-items: center;
}
ul li:nth-child(2) {
  border-top: solid #b2b2b2 0.001em;
}
ul li:last-child {
  border-bottom: solid #b2b2b2 0.001em;
}
ul li img {
  height: 13px;
  vertical-align: middle;
  position: relative;
  opacity: 0.4;
}
ul li label {
  width: 98%;
}
ul li.picker label {
  width: 80%;
}
ul li.setting label {
  width: 80%;
}
ul li.setting input {
  width: 15%;
  text-align: right;
  font-size: 16px;
  opacity: 0.4;
  border: none;
}
ul li.picker span {
  text-align: right;
  width: 20%;
  opacity: 0.4;
}
ul li.setting span {
  text-align: right;
  width: 5%;
  opacity: 0.4;
}
ul li.toggle label {
  width: 50px;
}
ul li.toggle span {
  width: -webkit-fill-available;
}
@media (prefers-color-scheme: dark) {
  #settingsView {
    background: #191926;
  }
  #header  {
    background: #191926;
    border-bottom: 1px solid #2d2d39;
  }
  ul {
    background: #191926;
  }
  ul li:first-child {
    color: #8e8e92;
    background: #191926;
  }
  ul li {
    background: #191926;
    color: #e5e5fa;
    border-bottom: solid #2d2d39 0.001em;
  }
  ul li.setting input {
    background: #191926;
    color: #cccccc;
  }
  ul li:nth-child(2) {
    border-top: solid #2d2d39 0.001em;
  }
  ul li:last-child {
    border-bottom: solid #2d2d39 0.001em;
  }
}
</style>
