<template>
  <div id="drinkView">
    <button id="undoButton" @click="undoAction" :style="undoIcon"></button>
    <div id="waterDropContainer">
      <div id="waterDrop" @click="addVal(100)">
        <div class="ocean" :style="`height: ${dailyTrackCount}%`">
          <div class="wave"></div>
          <div class="wave"></div>
        </div>
      </div>
    </div>
    <p id="progressMeter">{{ dailyTrackCount }}%</p>
    <div id="addActionContainer">
      <button class="addButton" id="" v-if="showAddMenu" @click="addAction(smallGulpResult)" :style="smallAdd"></button>
      <button id="addToogle" @click="toogleAddButton" :style="toogleIcon"></button>
      <button class="addButton" v-if="showAddMenu" @click="addAction(bigGulpResult)" :style="largeAdd"></button>
    </div>
  </div>
</template>

<script>
//import VueAnime from 'vue-animejs'
import anime from 'animejs';
import { TrackingService } from "@/services/TrackingService"
import { SettingsService } from "@/services/SettingsService"

export default {
  name: "Drink",
  components: {
    //VueAnime,
    //anime
  },
  data () {
    return {
      trackingItems: [],
      settingsItems: [],
      smallGulp: "0,2",
      bigGulp: "0,5",
      dailyGoal: "2",
      undoIcon: {backgroundImage: 'url(' + require(`../assets/undoIcon.svg`) + ')'},
      toogleIcon: {backgroundImage: 'url(' + require(`../assets/addIcon.svg`) + ')'},
      smallAdd: {backgroundImage: 'url(' + require(`../assets/smallAddButton.svg`) + ')'},
      largeAdd: {backgroundImage: 'url(' + require(`../assets/largeAddButton.svg`) + ')'},
      showAddMenu: false,
      smallGulpResult: 1,
      bigGulpResult: 1,
      dailyTrack: 0,
      dailyTrackCount: 0,
    }
  },
  watch: {
    dailyTrack(val) {
      this.setCount(val)
    }
  },
  mounted () {
    this.fetchTrackingData()
    this.smallGulp = this.settingsItems.find(item => item.id === "smallGulp").value
    this.bigGulp = this.settingsItems.find(item => item.id === "bigGulp").value
    this.dailyGoal = this.settingsItems.find(item => item.id === "dailyGoal").value
    this.smallGulpResult = parseFloat(this.smallGulp.replace( /,/,'.'))*100/parseFloat(this.dailyGoal.replace( /,/,'.'));
    this.bigGulpResult = parseFloat(this.bigGulp.replace( /,/,'.'))*100/parseFloat(this.dailyGoal.replace( /,/,'.'));
    this.setCount(this.dailyTrack);
    this.dailyTrack = this.getDailyTrack()
  },
  methods: {
    getDailyTrack() {
      var dailyItems = this.trackingItems.filter(item => new Date(item.createdAt).getDate() === new Date().getDate())
      return dailyItems.reduce((acc, item) => acc + item.value, 0);
    },
    addVal(n) {
      this.dailyTrack += n
    },
    removeVal(n) {
      this.dailyTrackCount = this.dailyTrackCount - n
      this.dailyTrack = this.dailyTrack - n
    },
    setCount(val) {
      const obj = { n: this.dailyTrackCount }
      anime({
        targets: obj,
        n: val,
        round: 1,
        duration: 250,
        easing: 'linear',
        update: () => {
          this.dailyTrackCount = obj.n
        }
      })
    },
    undoAction() {
      if (confirm("Letzte Aktion rückgängig machen?") == true) {
        if ((this.trackingItems.length > 0) && (new Date(this.trackingItems[this.trackingItems.length - 1].createdAt).getDate() === new Date().getDate())) {
          this.removeVal(this.trackingItems[this.trackingItems.length - 1].value)
          this.trackingItems.pop();
          this.storeItems()
        }
      }
    },
    toogleAddButton() {
      this.showAddMenu = !this.showAddMenu
      if (this.showAddMenu) {
        this.toogleIcon = {backgroundImage: 'url(' + require(`../assets/addIcon_active.svg`) + ')'}
      } else {
        this.toogleIcon = {backgroundImage: 'url(' + require(`../assets/addIcon.svg`) + ')'}
      }
    },
    addAction: function (trackingValue) {
      // do nothing if we have no content
      // push a new item to the list
      this.trackingItems.push({
        id: Math.random().toString(36).substr(2, 7),
        value: trackingValue,
        source: "Drink!",
        createdAt: Date.now()
      })
      // store the items
      this.storeItems()
      this.toogleAddButton()
      this.addVal(trackingValue)
    },
    fetchTrackingData () {
      this.trackingItems = TrackingService.fetchAll()
      this.settingsItems = SettingsService.fetchAll()
    },
    storeItems() {
      TrackingService.storeAll(this.trackingItems)
    }
  }
};
</script>

<style>
#drinkView {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: -webkit-fill-available;
  overflow: hidden !important;
  margin: 100px 0 0;
  white-space: nowrap;
}
#undoActionContainer {
  height: 5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  min-height: 30px;
  margin: 40px 0 0 0;
  width: 100%;
}
#undoButton {
  position: fixed;
  border: none;
  background: transparent;
  height: 26px;
  width: 26px;
  right: 20px;
  top: 60px
}
#waterDropContainer {
  width: 100%;
  position: fixed;
  top: 30%;
}
#waterDrop {
  width: 200px;
  height: 200px;
  background-color: #e8e8ea;
  border-radius: 50%;
  border: 5px solid #fff;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  -webkit-mask-image: -webkit-radial-gradient(white, black);
  mask-image: -webkit-radial-gradient(white, black);
}
#progressMeterContainer {
  height: 25%;
}
#progressMeter {
  font-family: "Kaushan Script", cursive;
  font-size: 46px;
  font-weight: 700;
  color: #327bd0;
  padding: 5% 0 0;
  position: fixed;
  width: 100%;
  top: 50%;
}
#addActionContainer {
  position: fixed;
  width: 100%;
  bottom: 100px;
  height: 100px;
}
#addToogle {
  border: none;
  background: transparent;
  height: 70px;
  width: 70px;
  -webkit-transition: background-image 0.2s linear;
  transition: background-image 0.2s linear;
  bottom: 120px;
  margin: 0 auto;
}
.addButton {
  border: none;
  background: transparent;
  height: 70px;
  width: 70px;
  margin: 0 20px;
  bottom: 120px;
}
/* Water Drop */
.ocean { 
  width:400px;
  position:absolute;
  bottom: 0px;
  background: #015871;
  transition: height 0.25s ease-out;
  left: -100px;
}

.wave {
  background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/85486/wave.svg) repeat-x; 
  position: absolute;
  top: -24px;
  width: 2000px;
  height: 60px;
  animation: wave 9s cubic-bezier( 0.36, 0.45, 0.63, 0.53) infinite;
  transform: translate3d(0, 0, 0);
  background-size: 200px;
}

.wave:nth-of-type(2) {
  top: -20px;
  animation: wave 8s cubic-bezier(0.36, 0.45, 0.63, 0.53) -.125s infinite,
    swell 7s ease -1.25s infinite;
  opacity: 1;
}
@keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}
@keyframes swell {
  0%,
  100% {
    transform: translate3d(0, -5px, 0);
  }
  50% {
    transform: translate3d(0, 5px, 0);
  }
}
@media (max-height: 600px) and (orientation: portrait) {
  #undoButton {
    top: 30px;
  }
  #waterDropContainer {
    top: 15%;
  }
  #progressMeter {
    font-size: 40px;
    top: 44%;
  }
  #addActionContainer {
    bottom: 80px;
  }
}
@media (max-height: 700px) and (orientation: portrait) {
  #undoButton {
    top: 30px;
  }
  #waterDropContainer {
    top: 15%;
  }
  #progressMeter {
    font-size: 40px;
    top: 44%;
  }
  #addActionContainer {
    bottom: 80px;
  }
}
@media (min-height: 700px) and (max-height: 800px) and (orientation: portrait) {
  #undoButton {
    top: 45px;
  }
  #waterDropContainer {
    top: 20%;
  }
  #progressMeter {
    font-size: 40px;
    top: 50%;
  }
  #addActionContainer {
    bottom: 80px;
  }
}
@media (prefers-color-scheme: dark) {
  #waterDrop {
    background: #413f4b;
    border: 5px solid #191926;
  }
}
</style>
